
.body{
    align-items: center;
     display:flex;
    flex-direction: column;
}

.tabs{
    display:flex; 
    margin-top:150px; 
    margin-left:20%;
    margin-right:20%;
    
}
.tab{
    cursor: pointer;
    color:white;
    font-size: 30px;
   margin-bottom:10px;
   margin-left:25px;
   margin-right:25px;
}
.tab:hover {
    color:aqua;
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
}
.cards{
    display:flex;
    margin-top:80px;
    
}
.card {
    margin-left:10px;
    margin-top:30px;
    justify-content: center;
    height:300px;
    width:444px;
    background-color: rgb(245, 245, 245);
    box-shadow: 2px 2px gray;
}

.card-bottom {
    padding-top: 5%;
    display:flex;
    flex-direction: column;
    justify-content:space-between;
    align-items: center;
}
.card-bottom div {
 text-align: center;
    font-size: 20px;

}
.react-card-bottom {
    font-size: 20px;
    padding-top: 5%;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.react-card-bottom a{
     padding-top: 10%;
    font-size:30px;
    color:aqua;
    text-decoration: none;
}

.card-bottom a {
    padding-top: 2%;
    font-size:30px;
    color:aqua;
    text-decoration: none;

}
.title{
    text-align: center;
}

.line {
    background-color:black;
    width:100%;
    height:5px;
}
